
@import "~normalize.css/normalize.css";
body{background:#eee
}
#app{font-family:'Avenir',Helvetica,Arial,sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-align:center;color:#657180;width:100%;position:relative;padding:0;margin:0;overflow:hidden;top:0
}
header{z-index:999;position:absolute;left:0;right:0
}
header ul{list-style:none;overflow:hidden;padding:0 10px;margin:0
}
header ul li{display:inline-block;height:20px;line-height:20px;padding:20px 10px;float:left
}
header ul li>h3{display:inline-block;padding:0;margin:0;font-weight:100
}
header ul li.right{float:right
}
a{color:#3399ff;text-decoration:none
}
a:hover{color:#5cadff
}
button,.button{padding:7px 14px;margin:7px;background:0;box-shadow:0 1px 0 0 rgba(0,0,0,0.1);color:#657180;background:#fff;opacity:.7;border-radius:3px;cursor:pointer;display:inline-block
}
button.dark,.button.dark{color:#e3e8ee;border:solid 1px #e3e8ee;background:none
}
button.warning,.button.warning{color:#ff6600
}
button:hover,.button:hover{opacity:1
}
input{outline:none
}


